<template><section>
<h1>Dialog 对话框</h1>
<p>在保留当前页面状态的情况下，告知用户并承载相关操作。</p>
<hr>
<h3>基础用法</h3>
<p>Dialog 弹出一个对话框，适合需要定制性更大的场景。</p>
<div class="demo-block">
    <xc-button type="text" @click="dialogVisible = true">点击打开 Dialog</xc-button>
    <xc-dialog title="提示" @open="open" @close="close" :visible.sync="dialogVisible" :before-close="handleClose" width="30%">
        <span>这是一段信息</span>
        <span slot="footer" class="dialog-footer">
        <xc-button @click="dialogVisible = false">取 消</xc-button>
        <xc-button type="primary" @click="dialogVisible = false">确 定</xc-button>
  </span>
    </xc-dialog>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
   <xc-button type="text" @click="dialogVisible = true">点击打开 Dialog</xc-button>
    <xc-dialog title="提示" @open="open" @close="close" :visible.sync="dialogVisible" :before-close="handleClose" width="30%">
        <span>这是一段信息</span>
        <span slot="footer" class="dialog-footer">
        <xc-button @click="dialogVisible = false">取 消</xc-button>
        <xc-button type="primary" @click="dialogVisible = false">确 定</xc-button>
  </span>
    </xc-dialog>
</template>



</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisible = true"</span>&gt;</span>点击打开 Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-dialog</span>
        <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>
        <span class="hljs-attr">@open</span>=<span class="hljs-string">"open"</span>
        <span class="hljs-attr">@close</span>=<span class="hljs-string">"close"</span>
        <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"dialogVisible"</span>
        <span class="hljs-attr">:before-close</span>=<span class="hljs-string">"handleClose"</span>
        <span class="hljs-attr">width</span>=<span class="hljs-string">"30%"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>这是一段信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dialog-footer"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisible = false"</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dialogVisible = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xc-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">dialogVisible</span>: <span class="hljs-literal">false</span>
            }
        },
        <span class="hljs-attr">methods</span>: {
            handleClose(done) {
                <span class="hljs-keyword">let</span> result = confirm(<span class="hljs-string">"确定关闭吗？"</span>);
                <span class="hljs-keyword">if</span>(result) done();
            },
            open() {},
            close() {}
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>嵌套的 Dialog</h3>
<p>如果需要在一个 Dialog 内部嵌套另一个 Dialog，需要使用 append-to-body 属性。</p>
<div class="demo-block">
    <xc-button type="text" @click="outerVisible = true">点击打开外层 Dialog</xc-button>
    <xc-dialog title="外层 Dialog" :visible.sync="outerVisible">
        <xc-dialog width="30%" title="内层 Dialog" :visible.sync="innerVisible">
        </xc-dialog>
        <span slot="footer" class="dialog-footer">
            <xc-button @click="outerVisible = false">取 消</xc-button>
            <xc-button type="primary" @click="innerVisible = true">打开内层 Dialog</xc-button>
        </span>
    </xc-dialog>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-button type="text" @click="outerVisible = true">点击打开外层 Dialog</xc-button>
    <xc-dialog title="外层 Dialog" :visible.sync="outerVisible">
        <xc-dialog width="30%" title="内层 Dialog" :visible.sync="innerVisible">
        </xc-dialog>
        <span slot="footer" class="dialog-footer">
            <xc-button @click="outerVisible = false">取 消</xc-button>
            <xc-button type="primary" @click="innerVisible = true">打开内层 Dialog</xc-button>
        </span>
    </xc-dialog>
</template>



</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"outerVisible = true"</span>&gt;</span>点击打开外层 Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-dialog</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"外层 Dialog"</span> <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"outerVisible"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-dialog</span>
       <span class="hljs-attr">width</span>=<span class="hljs-string">"30%"</span>
       <span class="hljs-attr">title</span>=<span class="hljs-string">"内层 Dialog"</span>
       <span class="hljs-attr">:visible.sync</span>=<span class="hljs-string">"innerVisible"</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">xc-dialog</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dialog-footer"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"outerVisible = false"</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"innerVisible = true"</span>&gt;</span>打开内层 Dialog<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xc-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">outerVisible</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">innerVisible</span>:  <span class="hljs-literal">false</span>
            }
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>visible</td>
<td>是否显示 Dialog，支持 .sync 修饰符</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>Dialog 的标题,也可通过具名 slot 传入</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>top</td>
<td>Dialog CSS 中的 margin-top 值</td>
<td>string</td>
<td>—</td>
<td>15vh</td>
</tr>
<tr>
<td>width</td>
<td>Dialog 的宽度</td>
<td>string</td>
<td>—</td>
<td>50%</td>
</tr>
<tr>
<td>modal</td>
<td>是否需要遮罩层</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>close-on-click-modal</td>
<td>是否可以通过点击 modal 关闭 Dialog</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>before-close</td>
<td>关闭前的回调，会暂停 Dialog 的关闭</td>
<td>function(done)，done 用于关闭 Dialog</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>Slot</h3>
<table class="table"><thead>
<tr>
<th>name</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>—</td>
<td>Dialog 的内容</td>
</tr>
<tr>
<td>title</td>
<td>Dialog 标题区的内容</td>
</tr>
<tr>
<td>footer</td>
<td>Dialog 按钮操作区的内容</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>Dialog 关闭的回调</td>
<td>—</td>
</tr>
<tr>
<td>open</td>
<td>Dialog 打开的回调</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data() {
            return {
                dialogVisible: false,
                outerVisible: false,
                innerVisible:  false
            }
        },
        methods: {
            handleClose(done) {
                let result = confirm("确定关闭吗？");
                if(result) done();
            },
            open() {},
            close() {}
        }
    };
</script>


