<template><section>
<h1>Checkbox 多选框</h1>
<p>一组备选项中进行多选</p>
<hr>
<h3>基础用法</h3>
<p>单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。</p>
<p>在<code v-pre="">xc-checkbox</code>元素中定义<code v-pre="">v-model</code>绑定变量，单一的<code v-pre="">checkbox</code>中，默认绑定变量的值会是<code v-pre="">Boolean</code>，选中为<code v-pre="">true</code>。</p>
<div class="demo-block">
    <xc-checkbox v-model="checked">备选项</xc-checkbox>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-checkbox v-model="checked">备选项</xc-checkbox>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
            };
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>多选框不可用状态。</p>
<p>设置<code v-pre="">disabled</code>属性即可。</p>
<div class="demo-block">
    <xc-checkbox v-model="checked1" disabled>备选项</xc-checkbox>
    <xc-checkbox v-model="checked2" disabled>备选项</xc-checkbox>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-checkbox v-model="checked1" disabled>备选项</xc-checkbox>
    <xc-checkbox v-model="checked2" disabled>备选项</xc-checkbox>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked1"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">checked1</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">checked2</span>: <span class="hljs-literal">true</span>
            };
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>多选框组</h3>
<p>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</p>
<p><code v-pre="">checkbox-group</code>元素能把多个 <code v-pre="">checkbox</code> 管理为一组，只需要在 <code v-pre="">Group</code> 中使用<code v-pre="">v-model</code>绑定<code v-pre="">Array</code>类型的变量即可。 <code v-pre="">xc-checkbox</code> 的 <code v-pre="">label</code>属性是该 <code v-pre="">checkbox</code> 对应的值，若该标签中无内容，则该属性也充当<code v-pre="">checkbox</code> 按钮后的介绍。<code v-pre="">label</code>与数组中的元素值相对应，如果存在指定的值则为选中状态，否则为不选中。</p>
<div class="demo-block">
    <xc-checkbox-group v-model="checkList">
        <xc-checkbox label="复选框 A"></xc-checkbox>
        <xc-checkbox label="复选框 B"></xc-checkbox>
        <xc-checkbox label="复选框 C"></xc-checkbox>
        <xc-checkbox label="禁用" disabled></xc-checkbox>
        <xc-checkbox label="选中且禁用" disabled></xc-checkbox>
    </xc-checkbox-group>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-checkbox-group v-model="checkList">
        <xc-checkbox label="复选框 A"></xc-checkbox>
        <xc-checkbox label="复选框 B"></xc-checkbox>
        <xc-checkbox label="复选框 C"></xc-checkbox>
        <xc-checkbox label="禁用" disabled></xc-checkbox>
        <xc-checkbox label="选中且禁用" disabled></xc-checkbox>
    </xc-checkbox-group>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkList"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 A"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 B"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"复选框 C"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"禁用"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选中且禁用"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">checkList</span>: [<span class="hljs-string">'选中且禁用'</span>,<span class="hljs-string">'复选框 A'</span>]
            };
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>indeterminate 状态</h3>
<p><code v-pre="">indeterminate</code> 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果</p>
<div class="demo-block">
    <xc-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</xc-checkbox>
    <div style="margin: 15px 0;"></div>
    <xc-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
        <xc-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</xc-checkbox>
    </xc-checkbox-group>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</xc-checkbox>
    <div style="margin: 15px 0;"></div>
    <xc-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
        <xc-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</xc-checkbox>
    </xc-checkbox-group>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"isIndeterminate"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkAll"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckAllChange"</span>&gt;</span>全选<span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin: 15px 0;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedCities"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleCheckedCitiesChange"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-checkbox</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"city in cities"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"city"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"city"</span>&gt;</span>{{city}}<span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xc-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">const</span> cityOptions = [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>, <span class="hljs-string">'广州'</span>, <span class="hljs-string">'深圳'</span>];

    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">checkAll</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">checkedCities</span>: [<span class="hljs-string">'上海'</span>, <span class="hljs-string">'北京'</span>],
                <span class="hljs-attr">cities</span>: cityOptions,
                <span class="hljs-attr">isIndeterminate</span>: <span class="hljs-literal">true</span>
            };
        },
        <span class="hljs-attr">methods</span>: {
            handleCheckAllChange(val) {
                <span class="hljs-keyword">this</span>.checkedCities = val ? cityOptions : [];
                <span class="hljs-keyword">this</span>.isIndeterminate = <span class="hljs-literal">false</span>;
            },
            handleCheckedCitiesChange(value) {
                <span class="hljs-keyword">let</span> checkedCount = value.length;
                <span class="hljs-keyword">this</span>.checkAll = checkedCount === <span class="hljs-keyword">this</span>.cities.length;
                <span class="hljs-keyword">this</span>.isIndeterminate = checkedCount &gt; <span class="hljs-number">0</span> &amp;&amp; checkedCount &lt; <span class="hljs-keyword">this</span>.cities.length;
            }
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Checkbox Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>选中状态的值（只有在checkbox-group或者绑定对象类型为array时有效）</td>
<td>string / number / boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>name</td>
<td>原生 name 属性</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>checked</td>
<td>当前是否勾选</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>设置 indeterminate 状态，只负责样式控制</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Checkbox Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当绑定值变化时触发的事件</td>
<td>更新后的值</td>
</tr>
</tbody>
</table>
<h3>Checkbox-group Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>Checkbox-group Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当绑定值变化时触发的事件</td>
<td>更新后的值</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    const cityOptions = ['上海', '北京', '广州', '深圳'];
    export default {
        data() {
            return {
                checked: true,
                checked1: false,
                checked2: true,
                checkList: ['选中且禁用','复选框 A'],
                checkAll: false,
                checkedCities: ['上海', '北京'],
                cities: cityOptions,
                isIndeterminate: true
            };
        },
        methods: {
            handleCheckAllChange(val) {
                this.checkedCities = val ? cityOptions : [];
                this.isIndeterminate = false;
            },
            handleCheckedCitiesChange(value) {
                let checkedCount = value.length;
                this.checkAll = checkedCount === this.cities.length;
                this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
            }
        }
    };
</script>


