<template><section>
<h1>Radio 单选框</h1>
<p>在一组备选项中进行单选</p>
<hr>
<h3>基础用法</h3>
<p>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
<p>要使用 <code v-pre="">Radio</code> 组件，只需要设置<code v-pre="">v-model</code>绑定变量，选中意味着变量的值为相应 <code v-pre="">Radio label</code>属性的值，<code v-pre="">label</code>可以是<code v-pre="">String</code>、<code v-pre="">Number</code>或<code v-pre="">Boolean</code>。</p>
<div class="demo-block">
    <xc-radio v-model="radio" label="1">备选项</xc-radio>
    <xc-radio v-model="radio" label="2">备选项</xc-radio>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-radio v-model="radio" label="1">备选项</xc-radio>
    <xc-radio v-model="radio" label="2">备选项</xc-radio>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                 <span class="hljs-attr">radio</span>: <span class="hljs-string">'1'</span>
            };
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>单选框不可用的状态</p>
<p>只要在<code v-pre="">xc-radio</code>元素中设置<code v-pre="">disabled</code>属性即可，它接受一个<code v-pre="">Boolean</code>，<code v-pre="">true</code>为禁用。</p>
<div class="demo-block">
    <xc-radio disabled v-model="radio1" label="禁用">备选项</xc-radio>
    <xc-radio disabled v-model="radio1" label="选中且禁用">备选项</xc-radio>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-radio disabled v-model="radio1" label="禁用">备选项</xc-radio>
    <xc-radio disabled v-model="radio1" label="选中且禁用">备选项</xc-radio>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"禁用"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选中且禁用"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                 <span class="hljs-attr">radio1</span>: <span class="hljs-string">'选中且禁用'</span>
            };
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>单选框组</h3>
<p>适用于在多个互斥的选项中选择的场景</p>
<div class="demo-block">
    <xc-radio-group v-model="radio2">
        <xc-radio :label="3">备选项</xc-radio>
        <xc-radio :label="6">备选项</xc-radio>
        <xc-radio :label="9">备选项</xc-radio>
        <xc-radio :label="12" disabled>备选项</xc-radio>
    </xc-radio-group>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
    <xc-radio-group v-model="radio2">
        <xc-radio :label="3">备选项</xc-radio>
        <xc-radio :label="6">备选项</xc-radio>
        <xc-radio :label="9">备选项</xc-radio>
        <xc-radio :label="12" disabled>备选项</xc-radio>
    </xc-radio-group>
</template>


</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
