        <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"3"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"6"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"9"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xc-radio</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"12"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xc-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                 <span class="hljs-attr">radio2</span>: <span class="hljs-string">'3'</span>
            };
        }
    };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Radio Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>Radio 的 value</td>
<td>string / number / boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>name</td>
<td>原生 name 属性</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>Radio Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当绑定值变化时触发的事件</td>
<td>更新后的值</td>
</tr>
</tbody>
</table>
<h3>Radio-group Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody></tbody>
</table>
